.panel-container {
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  border-radius: 4px;
  font-size: 90%;
  @include center;
  padding: 30px;
  max-width: 400px;

  strong {
    line-height: 20px;
  }

  .ratings-container {
    display: flex;
    margin: 20px 0;

    .rating {
      flex: 1;
      cursor: pointer;
      padding: 20px;
      margin: 10px 5px;
      @include center;

      &:hover,
      &.active {
        border-radius: 4px;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);

        small {
          color: #111;
        }
      }

      img {
        width: 40px;
      }

      small {
        color: #555;
        display: inline-block;
        margin: 10px 0 0;
      }
    }
  }

  .btn {
    background-color: #302d2b;
    color: #fff;
    border: 0;
    border-radius: 4px;
    padding: 12px 30px;
    cursor: pointer;

    &:focus {
      outline: 0;
    }

    &:active {
      transform: scale(0.98);
    }
  }
}

.fa-heart {
  color: red;
  font-size: 30px;
  margin-bottom: 10px;
}
